
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/dist/renderer/ifDefined.js';
import { html, svg, repeat, classMap, styleMap } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';
const block0 = (context) => { return html`<button		type="button"		class="ui5-button-root"		?disabled="${ifDefined(context.disabled)}"		data-sap-focus-ref				dir="${ifDefined(context.rtl)}"		@focusout=${ifDefined(context._onfocusout)}		@focusin=${ifDefined(context._onfocusin)}		@click=${ifDefined(context._onclick)}		@mousedown=${ifDefined(context._onmousedown)}		tabindex=${ifDefined(context.tabIndexValue)}		aria-expanded="${ifDefined(context.accInfo.ariaExpanded)}"		aria-controls="${ifDefined(context.accInfo.ariaControls)}"		title="${ifDefined(context.accInfo.title)}"	>		${ context.icon ? block1(context) : undefined }<span id="${ifDefined(context._id)}-content" class="ui5-button-text"><bdi><slot></slot></bdi></span>		${ context.hasButtonType ? block2(context) : undefined }</button>`; };
const block1 = (context) => { return html`<ui5-icon				class="ui5-button-icon"				src="${ifDefined(context.icon)}"				show-tooltip=${ifDefined(context.iconOnly)}			></ui5-icon>		`; };
const block2 = (context) => { return html`<span class="ui5-hidden-text">${ifDefined(context.buttonTypeText)}</span>		`; };

export default block0;